import React from 'react';
import PropTypes from 'prop-types';

const Composer = ({ onSubmit, onChange, content }) => (<form id="message" onSubmit={onSubmit}>
  <textarea
    id="content"
    value={content}
    onBlur={() => this.onTyping(false)}
    onFocus={() => this.onTyping(true)}
    onChange={onChange} />
  <button type="submit">Send</button>
</form>);

Composer.propTypes = {
  onSubmit: PropTypes.func,
  onChange: PropTypes.func,
  content: PropTypes.string,
};

Composer.defaultProps = {
  onSubmit: () => undefined,
  onChange: () => undefined,
  content: '',
};

const Messages = (props) => {
  const { messages } = props;
  return messages.map(msg => (<li key={msg.id}>{msg.content}</li>));
};

Messages.propTypes = {
  messages: PropTypes.array,
};

const Messenger = (props) => {
  const { onSubmit, onChange, content, messages, typing } = props;
  return (<div>
    <ul>{[
      <Messages messages={messages} />,
      <li>Typing: {typing.join(', ') || 'no one.'}</li>]}</ul>
    <Composer
      content={content}
      onChange={onChange}
      onSubmit={onSubmit} />
  </div>);
};

Messenger.propTypes = Object.assign({
  typing: PropTypes.array,
}, Composer.propTypes, Messages.propTypes);

export default Messenger;



